import React, { useContext, ReactElement } from 'react';
import { CompanyProvider } from './CompanyContext';
import { Route, Routes, Navigate } from 'react-router-dom';
import Login from './pages/Login';
import Register from './pages/Register';
import { UserContext } from './UserContext';

import DashboardLayout from './components/DashboardLayout';
import Dashboard from './pages/Dashboard';
import BankingManagement from './pages/BankingManagement';
import APManagement from './pages/APManagement';
import ARManagement from './pages/ARManagement';
import Contacts from './pages/Contacts';
import ChartOfAccounts from './pages/ChartOfAccounts';
import Payroll from './pages/Payroll';
import Settings from './pages/Settings';
import UsersAndRoleSettings from './pages/UsersAndRoleSettings';

// ...existing code... (dashboard page moved to ./pages/Dashboard)

function ProtectedRoute({ children }: { children: ReactElement }) {
  const { user } = useContext(UserContext);
  if (!user) return <Navigate to="/login" replace />;
  return children;
}

function App() {
  return (
    <CompanyProvider>
    <Routes>
        <Route path="/login" element={<Login />} />
        <Route path="/register" element={<Register />} />
        <Route path="/" element={<Navigate to="/dashboard" replace />} />
        <Route
          path="/dashboard"
          element={
            <ProtectedRoute>
              <DashboardLayout>
                <Dashboard />
              </DashboardLayout>
            </ProtectedRoute>
          }
        />
        <Route
          path="/banking"
          element={
            <ProtectedRoute>
              <DashboardLayout>
                <BankingManagement />
              </DashboardLayout>
            </ProtectedRoute>
          }
        />
        <Route
          path="/ap"
          element={
            <ProtectedRoute>
              <DashboardLayout>
                <APManagement />
              </DashboardLayout>
            </ProtectedRoute>
          }
        />
        <Route
          path="/ar"
          element={
            <ProtectedRoute>
              <DashboardLayout>
                <ARManagement />
              </DashboardLayout>
            </ProtectedRoute>
          }
        />
        <Route
          path="/contacts"
          element={
            <ProtectedRoute>
              <DashboardLayout>
                <Contacts />
              </DashboardLayout>
            </ProtectedRoute>
          }
        />
        <Route
          path="/coa"
          element={
            <ProtectedRoute>
              <DashboardLayout>
                <ChartOfAccounts />
              </DashboardLayout>
            </ProtectedRoute>
          }
        />
        <Route
          path="/payroll"
          element={
            <ProtectedRoute>
              <DashboardLayout>
                <Payroll />
              </DashboardLayout>
            </ProtectedRoute>
          }
        />
        <Route
          path="/settings"
          element={
            <ProtectedRoute>
              <DashboardLayout>
                <Settings />
              </DashboardLayout>
            </ProtectedRoute>
          }
        />
        <Route
          path="/roles-settings"
          element={
            <ProtectedRoute>
              <DashboardLayout>
                <UsersAndRoleSettings />
              </DashboardLayout>
            </ProtectedRoute>
          }
        />
  {/* Fallback route for any other path */}
  <Route path="*" element={<Navigate to="/" replace />} />
    </Routes>
    </CompanyProvider>
  );
}

export default App;
